import { Component, OnInit } from '@angular/core';
import { SolicitudModel } from '../../../../commons/models/aerc-models/solicitud-model';
import { FormControl, FormGroup } from '@angular/forms';
import { SolicitudService } from '../../../../commons/services/aerc-services/solicitud.service';
import { UtilAlert } from '../../../../commons/util/util.alert';

@Component({
  selector: 'app-admin-fuera-plazo',
  templateUrl: './admin-fuera-plazo.component.html',
  styles: [
  ]
})
export class AdminSolicitudesFueraPlazoComponent implements OnInit {

  public solicitud = new SolicitudModel();
  public listadoSolicitudes: SolicitudModel[] = [];
  public formTopBar: FormGroup;

  constructor(
    private solicitudService: SolicitudService,
    private alert: UtilAlert,
  ) { }

  ngOnInit(): void {
    this.initForm();
  }

  initForm() {
    this.formTopBar = new FormGroup({
      idBanner: new FormControl('')
    });
  }

  buscar() {
    const idBanner = this.formTopBar.get('idBanner').value;
    this.listadoSolicitudes = [];
    if (idBanner == "") {
      this.alert.toastError('No ha ingresado un Id de Banner');
    } else {
      this.solicitudService.findAllSolicitudesByIdBanner(idBanner).subscribe(result => {
        // console.log(result);
        if (result.length != 0) {
          this.listadoSolicitudes = result;
        } else {
          this.alert.toastError('No se han encontrado solicitudes para el Id de Banner ' + idBanner);
        }
      })
    }
  }

  marcarFueraPlazo(marcar: boolean, solicitudTemp: SolicitudModel) {
    marcar ? solicitudTemp.isAprobadaFueraPlazo = true : solicitudTemp.isAprobadaFueraPlazo = null;
    this.solicitudService.marcarFueraPlazo(solicitudTemp).subscribe(result => {
      if (result) {
        this.alert.infoSwal("Solicitud actualizada correctamente.")
      }
    }, (_error) => {
      this.alert.toastError(_error?.error?.message)
    })
  }

  volver() {

  }

}
